.current-page {
  font-size: 1.5rem;
  vertical-align: middle;
}

.book-card-container {
  height: 60px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.display-book-detail {
  font-size: 0.9rem;
}

.book-author {
  font-size: 0.7rem;
}

.current-page,
.display-book-detail,
.book-author {
  line-height: 1;
}

/* Override some Bootstrap pagination styles */
ul.pagination {
  margin-top: 0;
  margin-bottom: 0;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}
ul.pagination li.page-item.active a.page-link {
  color: #445565 !important;
  background-color: #e3e7eb !important;
  border-color: #ced4da !important;
}
ul.pagination a.page-link {
  padding: 0.75rem 1rem;
  min-width: 3.5rem;
  text-align: center;
  box-shadow: none !important;
  border-color: #ced4da !important;
  color: #6b88a4;
  font-weight: 900;
  font-size: 1rem;
}
ul.pagination a.page-link:hover {
  background-color: #f4f4f4;
}
.spinner {
  padding-left: 50% !important;
  font-size: 32px;
  color: #00bcd4;
}
.search-bar {
  margin-top: 44px;
}